







import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action} from "vuex-class";

@Component
export default class NavMenu extends Vue {
  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];
  goToTab(tabName: string): void {
    this.$router.push({query: {tab: tabName}});
    this.updateTab(tabName);
  }

  tabs = [
    {title: "User Account Management", name: "UserAccountManagement"},
    {title: "Plan Management - admin", name: "PlanManagement"},
    {title: "Plan Setting - user", name: "PlanSetting"},
    {title: "Project Manager", name: "ProjectManager"},
    {title: "Project Worker", name: "ProjectWorker"},
    {title: "Sequence Manager - admin", name: "SequenceManager"},
    {title: "Sequence Manager - user ", name: "UserSequenceManager"},
    {title: "Sequence Store - admin", name: "SequenceStore"},
    {title: "Sequence Store - user", name: "UserSequenceStore"},
    {title: "My Labs", name: "Lab"},
    {title: "Transducer DB Settings", name: "TransducerDbSettings"},
    {title: "Transducer DB Device", name: "TransducerDbDevice"},
    {title: "Device Management", name: "IoT"},
    {title: "OTA", name: "OTA"},
    {title: "DemoEvent", name: "DemoEvent"},
  ];
}
